::-webkit-scrollbar{
  width: 12px;
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: rgb(55,150,131,0.7);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgb(55,150,131,1);
}

